    async ncftpgetss() {
      console.log(this.regsId);

      const res = await this.$http
        .post(
          `CarInspector/word/mark/${this.regsId}`,
          { headers: "application/x-download" },
          { responseType: "blob" }
        )
        .then((response) => {
          console.log(response);
          if (response.status === 200) {
            this.getUserlist();
            // 
            this.$message({
              type: "success",
              message: "",
            });
            const blob = new Blob([response.data], {
              type: "application/msword",
            });
            const fileName = ".pdf";

            const elink = document.createElement("a");

            elink.download = fileName;

            elink.style.display = "none";

            elink.href = URL.createObjectURL(blob);

            document.body.appendChild(elink);

            elink.click();

            URL.revokeObjectURL(elink.href); //URL
            document.body.removeChild(elink);
            this.dengdialogFormVisible = false;
          } else if (res.data.code == 201) {
            this.$message({
              type: "danger",
              message: "",
            });
          }
        })
        .catch((err) => {
          this.$message({
            type: "danger",
            message: "",
          });
          // _toastr.error(err)
        });
    },
    savencftpgetss() {
      this.loadingbutss = true;
      this.loadingbuttextss = "...";
      this.$http.get(`carword/mark?id=${this.shiyongId}`).then((res) => {
