        this.forma.provinceCode = ress.data.data.province;
        this.forms.provinceCode = ress.data.data.province;
        this.level = ress.data.data.city; //
        this.showCmds = false; //
        this.showCmd = true; //
        this.showCmd_1 = false; //
      }
      //carCylinder/selectAll
      //  carDoucument/select
      const res = await this.$http.get(
        `/carDoucument/select?carNum=${this.carNum}&pageSize=${this.pageSize}&currPage=${this.currPage}&regId=${this.regId}&unit=${this.unitName}&useName=${this.useName}&certification=${this.certification}&zoneName=${this.zoneName}`
      );
      console.log(res);
      this.userlist = res.data.data.list;
      this.total = res.data.data.totalCount;
    },
  },
  created() {
    this.getUserlist();
    this.administrative();
    this.form.regType = this.selects3[0].orders;
    this.form.carType = this.selects4[0].orders;
    this.form.equType = this.selects1[0].orders;
    this.form.productName = this.selects5[0].orders;
    // this.Municipal();
    // this.Municipals();
  },
  //   
  // components:{
  //    Message
  // }
};
</script>
